























import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    isRunning: false,
  }),

  methods: {
    async runSuite() {
      this.isRunning = true;
      await this.$store.dispatch('runSuite');
      // this.suite.executions.push(se);
      // this.suite.checks = se.checks;
      this.isRunning = false;
    },
  },

});
